export default abstract class SuperLister {
  private _lists: string[][];
  private _specifiedCount: number;

  constructor() {
    this._lists = [];
    this._specifiedCount = -1;
  }

  public generateLists(
    items: string[],
    specifiedCount: number = this._specifiedCount
  ): string[][] {
    this._lists.length = 0;
    this._lists.length = 0;
    this._specifiedCount = specifiedCount;
    if (this._specifiedCount < 0) {
      this._specifiedCount = 1;
    }
    if (0 < this._specifiedCount) {
      let partialItems: string[] = [];
      this.generateListsRecursively(items, partialItems);
    }
    return this._lists;
  }

  private generateListsRecursively(items: string[], partialItems: string[]): void {
    let storedPartialItems: string[] = [];
    for (let i: number = 0; i < items.length; i++) {
      storedPartialItems = partialItems.concat(items[i]);
      if (storedPartialItems.length < this._specifiedCount) {
        let slicedItems: string[] = [];
        slicedItems = this.concatSlicedItems(items, slicedItems, i);
        this.generateListsRecursively(slicedItems, storedPartialItems);
      } else {
        if (!this._lists.some((list) => {
          if (JSON.stringify(list) === JSON.stringify(storedPartialItems)) {
            return list;
          }
        })) {
          this._lists.splice(this._lists.length, 0, storedPartialItems);
        }
      }
    }
  }

  protected abstract concatSlicedItems(items: string[], slicedItems: string[], i: number): string[];
}