import SuperLister from './SuperLister';

export default class CombinationLister extends SuperLister {
  protected concatSlicedItems(items: string[], slicedItems: string[], i: number): string[] {
    return slicedItems.concat(items.slice(i + 1));
  };

  public static calc(numN: number, numR: number): number {
    let answer: number = 0;
    if (0 < numN && 0 < numR) {
      let aliasSmallR: number = numN - numR < numR
        ? numN - numR
        : numR;
      answer = 1;
      for (let i: number = numN - aliasSmallR + 1; i <= numN; i++) {
        answer *= (i / aliasSmallR);
        aliasSmallR--;
      }
    }
    return answer;
  }
}